___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-role {\n        background-color: #EEEEEE;\n        border-radius: 0 10px 10px 0;\n        color: #212121;\n        font-size: 20px;\n        padding: 17.5px 44px 17.5px 0;\n}\n.selected-org i {\n        font-size: 20px;\n        color: #BCBCBC;\n}\n.custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        left: -48px !important;\n}\n.custom-select .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n        font-size:16px\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n.input-group:hover .select-role .selected-role {\n        background-color: #E3E3E3;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/completeProfile.vue"],"names":[],"mappings":";AA+UI;QACI,yBAAyB;QACzB,4BAA4B;QAC5B,cAAc;QACd,eAAe;QACf,6BAA6B;AACjC;AACA;QACI,eAAe;QACf,cAAc;AAClB;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;QAC1C,sBAAsB;AAC1B;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,iBAAiB;QACjB;AACJ;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,yBAAyB;AAC7B","sourcesContent":["<script setup lang=\"ts\">\nimport { ref, onMounted, computed } from \"vue\";\nimport { container } from \"../../../domain/core/diContainer\";\nimport { INotification } from \"../../../domain/presentation/INotification\";\nimport { IRouter } from \"../../../domain/presentation/IRouter\";\nimport { IUseCaseExecutor } from \"../../../domain/useCase/IUseCaseExecutor\";\nimport { IUserUseCase } from \"../../../domain/useCase/IUserUseCase\";\nimport { IViewModel } from \"../../../domain/viewModel/IViewModel\";\nimport CustomSelect from \"../components/customSelect.vue\"\nimport CheckboxComp from \"../components/checkboxComp.vue\";\nimport { IAuthenticationUseCase } from \"../../../domain/useCase/IAuthenticationUseCase\";\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst userUseCase = container.resolve<IUserUseCase>(IUserUseCase);\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst router = container.resolve<IRouter>(IRouter);\nconst notification = container.resolve<INotification>(INotification);\n\n//const props = defineProps<{ redirectTo?: string }>();\nconst user = viewModel.user!;\nconst firstName = ref(viewModel.user!.name);\nconst lastName = ref(viewModel.user!.lastName);\nconst role = ref(viewModel.user!.role);\nconst emailConsent = ref(!!viewModel.user!.preferences?.emailConsent);\nconst redirectTo = (window as any).studio_redirectTo as string | undefined; //props.redirectTo;\nconst authentication = container.resolve<IAuthenticationUseCase>(IAuthenticationUseCase);\n(window as any).studio_redirectTo = undefined;\nconst roles = [\"Business Analyst\",\"Business Leader\",\"Citizen Developer\",\"Designer\",\"IT Leader\",\"Product Manager\", \"Software Architect\",\"Software Developer\",\"Other\"]\n\nconst IsThereAnyChange = computed(() => {\n    const user = viewModel.user!;\n    return !(user.name == firstName.value && user.lastName == lastName.value && user.role == role.value\n        && !!user.preferences?.emailConsent == emailConsent.value);\n})\n// onMounted(async () => {\n//     const user = viewModel.user!;\n//     firstName.value = user.name;\n//     lastName.value = user.lastName;\n//     role.value = user.role;\n// });\nfunction logout() {\n  executor.execute(async () => { await authentication.signOut(); }, { loading: true });\n}\nfunction updateProfile() {\n    executor.execute(async () => {\n        if (IsThereAnyChange) {\n            await userUseCase.updateUser({\n                profile: { name: firstName.value!, lastName: lastName.value!, role: role.value! },\n                preferences: { emailConsent: emailConsent.value }\n            });\n        }\n\n        if (redirectTo) { router.push(redirectTo); }\n    }, { loading: true });\n}\nfunction setRole (newRole: string) {\n    role.value = newRole;\n}\nfunction changeEmailconsentValue() {\n    emailConsent.value = !emailConsent.value;\n}\n\n</script>\n\n<template>\n    <div class=\"login-container container-fluid p-0 m-0\">\n        <img src=\"../../../images/login_background_1.svg\" alt=\"login_background\" class=\"login-background-1\">\n        <img src=\"../../../images/login_background_2.svg\" alt=\"login_background\" class=\"login-background-2\">\n        <img src=\"../../../images/login_background_3.svg\" alt=\"login_background\" class=\"login-background-3\">\n        <img src=\"../../../images/login_background_4.svg\" alt=\"login_background\" class=\"login-background-4\">\n        <img src=\"../../../images/login_background_5.svg\" alt=\"login_background\" class=\"login-background-5\">\n        <div class=\"form-wrap\">\n            <div class=\"form-content\">\n                <img src=\"../../../images/plateau_logo-red.svg\" alt=\"plateau_logo-red\" class=\"form-logo\">\n                <div class=\"text-container\">\n                    <h1 class=\"text-title\">One Last Step</h1>\n                </div>\n                <p v-if=\"redirectTo\" class=\"content\">Your account <b>{{ viewModel.user?.email }}</b> has been created. To proceed, provide your personal information and enhance your experience!</p>\n                <form action=\"javascript:void(0);\" id=\"form\">\n                    <div class=\"input-group mb-3\">\n                        <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-account-circle-outline mdi-24px\"></i></span>\n                        <input class='form-control' type=\"text\" v-model=\"firstName\" placeholder=\"First Name\" required='true' />\n                    </div>\n                    <div class=\"input-group mb-3\">\n                        <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-account-circle-outline mdi-24px\"></i></span>\n                        <input class='form-control' type=\"text\" v-model=\"lastName\" placeholder=\"Last Name\" required='true' />\n                    </div>\n                    <div class=\"input-group mb-3\">\n                        <span class=\"input-style\" id=\"basic-addon1\"><i class=\"mdi mdi-account-tie-outline mdi-24px\"></i></span>\n                        <CustomSelect  @input=\"setRole\" :selectedClass=\"'selected-role'\" :options=\"roles\" :placeholder=\"'Select Role'\" class=\"select-role form-control\" :default=\"user.role\"></CustomSelect>\n                    </div>\n                    <!-- <div style=\"display: flex; align-items: center; margin: 33px 0\">\n                        <CheckboxComp class=\"module-checkbox\" :check=\"emailConsent\" @click=\"changeEmailconsentValue()\" />\n                        <span class=\"comm-consent\">Communication Consent</span>\n                    </div> -->\n                    <div class=\"mb-4\">\n                        <div>\n                            <button :disabled=\"!IsThereAnyChange\" @click=\"updateProfile();\" class=\"submit-button\">SUBMIT AND LOGIN</button>\n                        </div>\n                    </div>\n                    <div class=\"bottom-content\" style=\"margin-top: 106px\">\n                        <div style=\"color:#A1A1A1; font-size:18px; text-align: center;margin-bottom: 35px;\">Have another account?</div>\n                        <div class=\"back-to-login\"><span @click=\"logout()\">Back to Login Page</span></div>\n                    </div>\n                </form>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n\n    * {\n        font-family: 'Roboto';\n    }\n    .form-wrap{\n        display: grid;\n        width: 772px;\n        position: absolute;\n        padding: 4% 146px;\n        right: 0;\n        background: #FFFFFF;\n        align-items: center;\n        height: 100vh;\n        overflow: auto;\n    }\n    body{\n        margin: 0;\n        padding: 0;\n        overflow: hidden;\n    }\n\n    .login-background-1, \n    .login-background-2, \n    .login-background-3, \n    .login-background-4, \n    .login-background-5 {\n        width: calc(100% - 772px);\n        height: 100%;\n        display:none;\n    }\n\n    .login-container {\n        display: flex;\n        background: #FFFFFF;\n    }\n\n    .form-logo{\n        width: 276px;\n        height: 49px;\n        margin: 0 auto 50px;\n        display: block;\n    }\n    .text-container {\n        margin-bottom: 16.5px;\n        opacity: 1;\n    }\n\n    .text-title {\n        margin-bottom: 1px;\n        text-align: left;\n        font-size: 32px;\n        font-family: \"Roboto\";\n        letter-spacing: 0px;\n        color: #151233;\n        opacity: 1;\n    }\n    .content {\n        text-align: left;\n        font-size: 22px;\n        font-family: \"Roboto\";\n        letter-spacing: 0px;\n        color: #757575;\n        opacity: 1;\n        margin-bottom: 41px;\n    }\n\n    .form-content {\n        margin-bottom: 1px;\n    }\n    input[type=\"text\"],\n    input[type=\"password\"] {\n        background-color: #eeeeee;\n        border-radius: 10px;\n        border: none;\n        font-size: 20px;\n        font-weight: normal;\n        letter-spacing: 0px;\n        color: #212121;\n        opacity: 1;\n    }\n    input::placeholder {\n    color: #616161;\n    }\n    .input-group:hover input,\n    .input-group:hover span,\n    .input-group:hover button,\n    .input-group:hover .select-role\n\n    {\n    background: #E3E3E3;\n    }\n    .input-group {\n        position: relative;\n        display: flex;\n        flex-wrap: wrap;\n        align-items: stretch;\n        width: 100%;\n    }\n\n    .input-style {\n        border: none;\n        background-color: #eeeeee;\n        padding: 14px 12px;\n        border-radius: 10px;\n        color: #4F537B;\n    }\n    .mdi-24px {\n        font-size: 24px;\n    }\n    .form-control {\n        padding: 17px 10px 17px 0;\n        appearance: auto;\n        -webkit-appearance: auto;\n        -moz-appearance: auto;\n    }\n    .form-control:focus {\n        background-color: #eeeeee !important;\n        box-shadow: none !important;\n    }\n\n    .form-control:disabled, .form-control[readonly]{\n        opacity: 1;\n    }\n    .module-checkbox {\n        margin-right: 28px;\n        margin-top: -18px;\n    }\n    .select-role {\n        width: 1%;\n        background-color: #eeeeee;\n        border-radius: 10px;\n        border: none;\n        color: #212121;\n        padding: 0;\n    }\n    .submit-button {\n        width: 100%;\n        font-size: 20px;\n        font-family: \"Roboto\";\n        font-weight: normal;\n        color: #ffffff;\n        background-color: #151233;\n        border: none !important;\n        border-radius: 10px;\n        padding: 17px;\n    }\n    .submit-button:disabled {\n        opacity: 0.5;\n    }\n    .comm-consent {\n        color: #212121;\n        font-size: 14px;\n        line-height: 16px;\n    }\n    ::-webkit-scrollbar-track {\n        background: #fafafa;\n    }\n\n    ::-webkit-scrollbar-thumb {\n        background: #BDBDBD;\n        border-radius: 8px;\n        border: 4px solid #FAFAFA;\n        background-clip: padding-box;\n    }\n    ::-ms-reveal {\n        display: none;\n    }\n    @media (max-width: 1280px){\n        .login-background-1 {\n        width: 508px;\n        display:block;\n        z-index: 0;\n        }\n    }\n    @media (min-width:1281px) and (max-width: 1440px){\n        .login-background-2 {\n        width: 666px;\n        display:block;\n        z-index: 0;\n        }\n    }\n    @media (min-width:1441px) and (max-width: 1680px){\n        .login-background-3 {\n        width: 908px;\n        display:block;\n        z-index: 0;\n        }\n    }\n    @media (min-width:1681px) and (max-width: 1920px) {\n        .login-background-4 {\n        width: 1148px;\n        display:block;\n        z-index: 0;\n        }\n    }\n    @media (min-width: 1921px) {\n        .login-background-5 {\n        width: 1638px;\n        display:block;\n        z-index: 0;\n        }\n    }\n    @media (max-width: 1024px){\n        .login-background-1,\n        .login-background-2,\n        .login-background-3,\n        .login-background-4,\n        .login-background-5 {\n        display: none;\n        }\n        .form-wrap {\n        width: 100%;\n        }\n    } \n    .back-to-login {\n            display: flex;\n            justify-content: center;\n            align-items: center;\n            font-size: 20px;\n            color: #151233;\n            font-weight: 500;\n            cursor: pointer;\n        }\n</style>\n<style>\n    .selected-role {\n        background-color: #EEEEEE;\n        border-radius: 0 10px 10px 0;\n        color: #212121;\n        font-size: 20px;\n        padding: 17.5px 44px 17.5px 0;\n    }\n    .selected-org i {\n        font-size: 20px;\n        color: #BCBCBC;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        left: -48px !important;\n    }\n    .custom-select .items .option {\n        color: #212121;\n        padding-left: 12px;\n        line-height: 32px;\n        font-size:16px\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .input-group:hover .select-role .selected-role {\n        background-color: #E3E3E3;\n    }\n</style>\n"],"sourceRoot":""}]);
